.wish-item {
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    width: 240px;
    height: 280px;
    // margin: 10px
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    border-radius: 15px;
    background: white;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23); }

    .wish-img-wrap {
        background: #c9c9c9;
        width: 100%;
        min-height: 180px;
        max-height: 180px;
        position: relative; }

    .wish-img {
        width: 100%;
        height: 100%;
        object-fit: cover; }

    .wish-price-wrap {
        position: absolute;
        bottom: 10px;
        right: 10px;
        background-color: #ffffff;
        border-radius: 20px;
        padding: 2px 5px;

        .wish-price {
            margin: 0; } }

    .wish-name {
        padding: 5px 10px;
        margin: 0;
        font-weight: 700; }

    .wish-link {
        margin: 0;
        padding: 5px 10px;
        font-weight: 300;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap; }


    .delete {
        position: absolute;
        top: 10px;
        right: 10px;
        cursor: pointer;
        border: none;
        margin: 0;
        padding: 1px 6px;
        width: auto;
        overflow: visible;
        background: #ffffff;
        border-radius: 50%;

        &:hover {
            color: red; } }

    .dropdown-wrap {
        position: absolute;
        right: 5px;
        top: 5px; }

    .dropdown {
        position: relative; }

    .dropbtn {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        padding: 3px;
        width: 20px;
        height: 20px;
        cursor: pointer;


        &:hover {
            background-color: #9c9c9c; }


        .dot {
            position: relative;
            margin: 1px;
            width: 3px;
            height: 3px;
            background-color: black;
            border-radius: 50%; } }


    .dropdown-content {
        display: none;
        position: absolute;
        background-color: #f1f1f1;
        min-width: 160px;
        overflow: auto;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 1; }

    .active {
        display: block; } }
