$white: #fff;
$google-blue: #4285f4;
$button-active-blue: #1669F2;


.login-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;


    .google-btn {
        padding: 0;
        width: 200px;
        height: 42px;
        background-color: $google-blue;
        border-radius: 2px;
        box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
        cursor: pointer;

        &:hover {
            box-shadow: 0 0 6px $google-blue; }

        &:active {
            background: $button-active-blue; } }


    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        background-color: $white; }

    .google-icon {
        position: absolute;
        margin-top: 11px;
        margin-left: 11px;
        width: 18px;
        height: 18px; }

    .btn-text {
        float: right;
        margin: 11px 11px 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px; } }




@import url(https://fonts.googleapis.com/css?family=Roboto:500);
